/*      (a   .link */
a {
    text-decoration: none;
    color: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
    margin: 0;
    padding: 0;
}

/*      (ul   .list) */
ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

/*      */
img {
    display: block;
    max-width: 100%;
}

.is-hidden {
    display: none !important;
}